@keyframes slideIn {
    from {
        transform: translateX(100%);
      }
      to {
        transform: translateX(0);
} }

@media screen and (max-width: 750px) {
    body {
        height: 100%;
    }

    h1 {
        text-align: center;
        font-size: 35px;
        padding-top: 25px;
    }
}